import React from 'react';
import styled from 'styled-components';

import {Container, SectionHeader} from 'ui';

import jumio from 'assets/images/logotypes/jumio.png';
import goodhire from 'assets/images/logotypes/goodhire.svg';
import hyperledger from 'assets/images/logotypes/hyperledger.png';
import linux from 'assets/images/logotypes/linux.png';
import enecuum from 'assets/images/logotypes/enecuum.svg';
import intercoin from 'assets/images/logotypes/intercoin.png';
import popchest from 'assets/images/logotypes/popchest.svg';

import solution from 'assets/images/partners/482-logo.png';
import crypto from 'assets/images/logotypes/crypto.png';
import modern from 'assets/images/partners/mt-logo.png';
import bazaar from 'assets/images/partners/logo--2-.png';

const Section = styled.section`
  padding: 0 0 100px 0;
`;

const PartnersList = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  list-style: none;
`;

const PartnersItem = styled.li`
  margin: 15px 20px;
`;

export const Partners = () => (
    <Section>
        <Container dir='column' align='center'>
            <SectionHeader margin='100px 0 40px'>Strategic partners</SectionHeader>
            <PartnersList>
                {partners.map(item => <PartnersItem key={item}><img src={item} alt=""/></PartnersItem>)}
            </PartnersList>
            <SectionHeader notIcon margin='100px 0 40px'>TGE partners</SectionHeader>
            <PartnersList>
                {tge.map(item => <PartnersItem key={item}><img src={item} alt=""/></PartnersItem>)}
            </PartnersList>
        </Container>
    </Section>
);

const partners = [
    jumio, goodhire, hyperledger, linux, enecuum, intercoin, popchest
];

const tge = [
    solution, crypto, modern, bazaar
];


