import React from 'react';
import styled from 'styled-components';

import {SectionHeader, Container} from 'ui';
import roadmapIcon from 'assets/images/honeycom-roadmap.svg';

const Section = styled.section`
  padding: 15% 0;
`;

const RoadmapCard = styled.div`
  width: 400px;
  padding: 12px 14px;
  box-shadow: 0 15px 26px rgba(47, 21, 136, 0.06), 0 0 11px rgba(47, 21, 136, 0.07);
  
  &:after, &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  
  &:before {
    height: 100%;
    width: 2px;
    background-color: #6743DF;
  }
  
  &:after {
    height: 15px;
    width: 15px;
    background: #fff url(${roadmapIcon}) no-repeat;
    background-size: cover;
  }
`;

const RoadmapListItem = styled.li`
  display: flex;
  position: relative;
  
  &:nth-child(odd) {
    text-align: right;
  }
  
  &:nth-child(even) {
    justify-content: flex-end;
  }
`;

const RoadmapList = styled.ul`
    display: flex;
    flex-direction: column;
    width: 80%;
    margin-top: 50px;
    list-style: none;
    
    h5 {
      margin-top: 0;
      margin-bottom: 10px;
      color: #6743DF;
      font-size: 1.125rem;
      font-weight: 700;
    }
    
    p {
      margin: 3px 0;
      color: #3E466D;
      font-size: .875rem;
    }
    
    @media (max-width: 1100px) {
      ${RoadmapCard} {
        width: 300px;
      }
    }
    
    @media (max-width: 850px) {
      width: 100%;
    }
    
    @media (max-width: 680px) {
      ${RoadmapCard} {
        width: 250px;
      }
    }
    
    @media (max-width: 570px) {
      ${RoadmapListItem} {
        padding: 10px 30px;
        text-align: left;
      }
      
      ${RoadmapCard} {
        width: 100%;
        
        &:after, &:before {
          left: 0;
          top: 23px;
        }
      }
    }
`;

export const Roadmap = () => (
    <Section>
        <Container dir='column' align='center'>
            <SectionHeader>Roadmap</SectionHeader>
            <RoadmapList>
                {roadmapData.map(item => (
                    <RoadmapListItem key={item.title}>
                        <RoadmapCard>
                            <h5>{item.title}</h5>
                            {item.process.map((item, index) => <p key={index}>{item}</p>)}
                        </RoadmapCard>
                    </RoadmapListItem>
                ))}
            </RoadmapList>
        </Container>
    </Section>
);

const roadmapData = [
    {title: 'Q3 2017', process: ['[Process] Idea inception and market analysis']},
    {title: 'Q4 2017', process: [
        '[Process] Blockchain scalability problem research',
        '[Process] Development of the platform core started',
    ]},
    {title: 'Q1 2018', process: [
        '[Process] Prototype release',
        '[Process] Incorporation in Delaware, USA',
        '[Process] Seed round: $350 000 raised'
    ]},
    {title: 'Q2 2018', process: [
        '[Process] Alpha release',
        '[Process] Blockchain technology finalization and integration'
    ]},
    {title: 'Q3 2018', process: [
        '[Process] Beta release',
        '[Process] Public app release (iOS, Android)',
        'Process] Patent filing in other countries',
        '[Industry] Auth plugins for popular platforms and frameworks',
        '(Shopify, Wordpress, Passport.js)'
    ]},
    {title: 'Q4 2018', process: [
        '[Process] Multi-language support',
        '[Process] Employment verification and screening',
        '[Process] Education & professional qualifications verification',
        '[Industry] Recruiting software integration (Lever, Greenhouse, TriNet, ZipRecruiter)'
    ]},
    {title: 'Q1 2019', process: [
        '[Geo] Expansion to Canada',
        '[Process] Developer community establishment',
        '[Industry] Vending machines & specialty ATMs'
    ]},
    {title: 'Q2 2019', process: [
        '[Geo] Expansion to Asia',
        '[Process] 3rd party developer SDK release',
        '[Industry] 3rd party auth solutions integration (Auth0, Okta, Duo Security)',
    ]},
    {title: 'Q3 2019', process: [
        '[Geo] Expansion to Europe',
        '[Industry] Corporate market solution (MFA, SSO, AD)',
        '[Industry] Dedicated FinTech solutions'
    ]},
    {title: 'Q4 2019', process: [
        '[Process] 3rd party app marketplace launch',
        '[Industry] Machine-to-machine auth integration (IoT devices)'
    ]},
];


