import React from 'react';
import styled from 'styled-components';

import {Container, SectionHeader} from 'ui';

import KyryloSopot from 'assets/images/photos/Kyrylo Sopot.png';
import KostiantynShterental from 'assets/images/photos/Kostiantyn Shterental.png';
import AlenaYudina from 'assets/images/photos/Alena Yudina.png';
import YuriyZnatokov from 'assets/images/photos/Yuriy Znatokov.png';
import YevgenYurash from 'assets/images/photos/Yevgen Yurash.png';

import IvanArabadzhy from 'assets/images/photos/Ivan Arabadzhy.png';
import ArtemMirchenko from 'assets/images/photos/Artem Mirchenko.png';
import AlexanderIvanov from 'assets/images/photos/Alexander Ivanov.png';
import SergeiPoznanski from 'assets/images/photos/Sergei Poznanski.png';
import SergeyZenkov from 'assets/images/photos/Sergey Zenkov.png';

import GaryBaiton from 'assets/images/photos/Gary Baiton.png';
import IsmailMalik from 'assets/images/photos/Ismail Malik.png';
import ColinBreeze from 'assets/images/photos/Colin Breeze.png';
import AndreyVerbitsky from 'assets/images/photos/Andrey Verbitsky.png';

import bg from 'assets/images/background-2.svg';

const Section = styled.section`
  background: url(${bg}) no-repeat 50% 50%;
`;

const LeadershipList = styled.ul`
  display: flex;
  justify-content: ${p => p.justify ? p.justify : 'space-around'};
  width: ${p => p.width ? p.width : '100%'};
  padding: ${p => p.padding ? p.padding : '80px 0'};
  
  @media (max-width: 930px) {
    flex-wrap: wrap;
  }
`;

const LeadershipItem = styled.li`
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 160px;
  text-align: center;
  position: relative;
  margin: 15px;
  
  img {
    width: 80px;
  }
  
  h5 {
    margin: 7px 0;
    color: #6743DF;
    font-size: .875rem;
    font-weight: 700;
  }
  
  p {
    margin: 0;
    color: #3E466D;
    font-size: .75rem;
    font-weight: 600;
    line-height: 1.125rem;
  }
`;

const FirstLeadershipItem = LeadershipItem.extend`
  &:nth-child(2) {
    top: -80px;
    right: -80px;
  }
  
  &:nth-child(3) {
    top: 80px;
  }
  
  &:nth-child(4) {
    top: -80px;
    right: 80px;
  }
  
  @media (max-width: 930px) {
    &:nth-child(2) {
    top: 0;
    right: 0;
  }
  
  &:nth-child(3) {
    top: 0;
  }
  
  &:nth-child(4) {
    top: 0;
    right: 0;
  }
  }
`;

const SecondLeadershipItem = FirstLeadershipItem.extend`
  &:nth-child(2), &:nth-child(4) {
    top: -80px;
    right: 0;
  }
  
  @media (max-width: 930px) {
    &:nth-child(2), &:nth-child(4) {
      top: 0;
    }
`;

const Title = styled.h5`
  color: #6743DF;
  font-size: 1.5rem;
  font-weight: 900;
  margin-top: 120px;
`;

export const Leadership = () => (
    <Section>
        <Container dir='column' align='center'>
            <SectionHeader margin='0 0 70px 0'>Leadership</SectionHeader>
            <LeadershipList>
                {leadership1.map(item => (
                    <FirstLeadershipItem key={item.name} >
                        <img src={item.img} alt=""/>
                        <h5>{item.name}</h5>
                        <p>{item.presentPost}</p>
                    </FirstLeadershipItem>
                ))}
            </LeadershipList>
            <LeadershipList>
                {leadership2.map(item => (
                    <SecondLeadershipItem key={item.name} >
                        <img src={item.img} alt=""/>
                        <h5>{item.name}</h5>
                        <p>{item.presentPost}</p>
                    </SecondLeadershipItem>
                ))}
            </LeadershipList>
            <Title>Advisors</Title>
            <LeadershipList padding='30px 0'>
                {advisors.map(item => (
                    <LeadershipItem key={item.name} >
                        <img src={item.img} alt=""/>
                        <h5>{item.name}</h5>
                        <p>{item.presentPost}</p>
                    </LeadershipItem>
                ))}
            </LeadershipList>
        </Container>
    </Section>
);

const leadership1 = [
    {name: 'Kyrylo Sopot', presentPost: 'VP of Business Development, LATAM', img: KyryloSopot},
    {name: 'Kostiantyn Shterental', presentPost: 'Co-Founder and CEO', img: KostiantynShterental},
    {name: 'Alena Yudina', presentPost: 'VP of Business Development, EMEA', img: AlenaYudina},
    {name: 'Yuriy Znatokov', presentPost: 'Co-Founder and CTO', img: YuriyZnatokov},
    {name: 'Yevgen Yurash', presentPost: 'VP of Business Development, APAC', img: YevgenYurash},
];

const leadership2 = [
    {name: 'Artem Mirchenko', presentPost: 'VP of Business Development, LATAM', img: ArtemMirchenko},
    {name: 'Ivan Arabadzhy', presentPost: 'Co-Founder and CEO', img: IvanArabadzhy},
    {name: 'Alexander Ivanov', presentPost: 'VP of Business Development, EMEA', img: AlexanderIvanov},
    {name: 'Sergei Poznanski', presentPost: 'Co-Founder and CTO', img: SergeiPoznanski},
    {name: 'Sergey Zenkov', presentPost: 'VP of Business Development, APAC', img: SergeyZenkov},
];

const advisors = [
    {name: 'Gary Baiton', presentPost: 'Growth Hacking', img: GaryBaiton},
    {name: 'Ismail Malik', presentPost: 'Blockchain R&D, Strategist', img: IsmailMalik},
    {name: 'Colin Breeze', presentPost: 'Senior Counsel', img: ColinBreeze},
    {name: 'Andrey Verbitsky', presentPost: 'Token Architect', img: AndreyVerbitsky},
];


