import React from 'react';
import styled from 'styled-components';

const IconedListItem = styled.li`
  margin-top: 40px;
  width: 40%;
`;

const IconedList = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  color: #4a527d;
  margin: 0;
  
  ${IconedListItem} {
    width: ${p => p.column ? "100%" : '40%'}
  }
  
  @media (max-width: 750px) {
    flex-direction: column;
    align-items: center;
    
    ${IconedListItem} {
      width: 80%;
    }
    
    @media (max-width: 500px) {
      ${IconedListItem} {
        width: 100%;
      }  
    }
  }
`;

const IconedListItemTitle = styled.h4`
  display: flex;
  align-items: center;
  padding: 3px 0;
  font-weight: 800;
  color: #4A527D;
  
  &:before {
    content: '';
    display: block;
    height: 30px;
    width: 30px;
    margin-right: 20px;
    background-size: cover;
    background-image: url(${p => p.img});
  }
`;

const IconedListItemDesc = styled.div`
  margin-top: 17px;
  font-size: .875rem;
  font-weight: 400;
  line-height: 1.375rem;
  color: ${p => p.color ? p.color : '#767FAB'};
  
  p {
    margin: 7px 0;
  }
`;

const Item = ({title, img, children, descColor}) => (
    <IconedListItem>
        <IconedListItemTitle img={img}>{title}</IconedListItemTitle>
        <IconedListItemDesc color={descColor}>{children}</IconedListItemDesc>
    </IconedListItem>
);

IconedList.Item = Item;

export {IconedList};


