import React from 'react';
import styled from 'styled-components';

import {Container} from 'ui';

const Title = styled.h1`
  font-size: 3.75rem;
`;

const SubTitle = styled.h3`
  margin-top: 32px;
  font-size: 1.5rem;
  opacity: .8;
`;

const HeroWrapper = styled.section`
  padding-top: 116px;
  min-height: 90vh;
  color: white;
  text-align: center;
  
  @media (max-width: 860px) {
    ${Title} {
      font-size: 2.75rem;
    }
  }
`;

export const Hero = () => (
    <HeroWrapper>
        <Container dir='column' align='center'>
            <Title>A Decentralized Identity<br/>Management<br/>Ecosystem</Title>
            <SubTitle>Powered by Blockchain Technology<br/>and Tokenization</SubTitle>
        </Container>
    </HeroWrapper>
);


