import React, {Component} from 'react';
import styled from 'styled-components';
import {colors} from 'ui/theme';

const TimerWrapper = styled.div`
  display: flex;
  font-size: ${p => p.size ? p.size : '2rem'};
  font-weight: bold;
`;

const TimerItem = styled.div`
  color: ${colors.main};
  margin: 0 10px;
  position: relative;
  padding-bottom: 16px;
  
  &:before {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    font-size: 1rem;
    color: #3e466d;
  }
`;

const TimerDays = TimerItem.extend`
  &:before {
    content: 'D';
  }
`;

const TimerHours = TimerItem.extend`
  &:before {
    content: 'H';
  }
`;

const TimerMinutes = TimerItem.extend`
  &:before {
    content: 'M';
  }
`;

const TimerSeconds = TimerItem.extend`
  &:before {
    content: 'S';
  }
`;

const Delimiter = styled.div`
  &:before, &:after {
    content: '';
    display: block;
    width: .15em;
    height: .15em;
    border-radius: 50%;
    background-color: ${colors.main};
  }
  
  &:after {
    display: ${p => p.one ? 'none' : 'block'};
    margin-top: 5px;
  }
`;

export class Timer extends Component {
    constructor(props) {
        super();

        this.state = {
            seconds: props.seconds ? +props.seconds : 0
        };
    }

    componentDidMount = () => {
        setInterval(() => {
            this.setState({seconds: this.state.seconds + (this.props.inverse ? -1 : 1)});
        }, 1000)
    };

    getSeconds = () => this.state.seconds % 60;
    getMinutes = () => this.state.seconds / 60 % 60 | 0;
    getHours = () => this.state.seconds / 60 / 60 % 24 | 0;
    getDays = () => this.state.seconds / 60 / 60 / 24 | 0;
    format = time => (time + '').padStart(2, '0');

    render() {
        return (
            <TimerWrapper {...this.props}>
                <TimerDays>{this.format(this.getDays())}</TimerDays>
                <Delimiter one/>
                <TimerHours>{this.format(this.getHours())}</TimerHours>
                <Delimiter/>
                <TimerMinutes>{this.format(this.getMinutes())}</TimerMinutes>
                <Delimiter/>
                <TimerSeconds>{this.format(this.getSeconds())}</TimerSeconds>
            </TimerWrapper>
        );
    }
}


