import React, {Component} from 'react';
import styled from 'styled-components';

import cover1 from 'assets/images/covers/cover-executive-summary.svg';
import cover2 from 'assets/images/covers/cover-white-paper.svg';
import cover3 from 'assets/images/covers/cover-onepager.svg';

import arrowDownImg from 'assets/images/arrow-down.svg';
import downloadImg from 'assets/images/dowload.svg';

const BooksContainer = styled.div`
  display: flex;
  flex-direction: column;
  box-shadow: 0 15px 26px rgba(50, 50, 93, 0.06), 0 0 11px rgba(0, 0, 0, 0.07);
  border-radius: 16px;
  background-color: #fff;
  padding: 50px;
  
  @media(max-width: 400px) {
    padding: 15px 5px;
  }
`;

const BooksTabs = styled.ul`
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    color: #6743DF;
    font-weight: 600;
    font-size: .875rem;
`;

const BooksTabsItem = styled.li`
  padding: 8px 16px;
  margin: 10px 10px;
  border-radius: 16px;
  background-color: #F6F4FD;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: color .2s;
  
  &:before {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 0;
      opacity: 0;
      left: 0;
      top: 0;
      transition: all .3s;
      background-color: #7956EF;
  }
  
  span {
    position: relative;
  }
  
  ${p => p.active && `
    color: white;
  
    &:before {
      height: 100%;
      opacity: 1;
    }
  `}
`;

const BookImage = styled.div`
  min-width: 228px;
  
  img {
    width: 100%;
  }
`;

const ChooseLang = styled.div`
  display: flex;
  align-items: center;
  margin: 20px 0;
  color: #504A8E;
  font-size: .75rem;
`;

const BookDesc = styled.div`
  padding: 5px 0 29px 25px;
  display: flex;
  flex-direction: column;
  
  h5 {
    color: #3E466D;
    font-size: 2rem;
    font-weight: 900;
    margin: 0;
  }
  
  p {
    color: #3E466D;
    font-size: .875rem;
    margin: 0;
  }
  
  a {
    margin-top: auto;
    display: flex;
    align-items: center;
    color: #6743DF;
    font-size: .875rem;
    transition: opacity .2s;
    font-weight: bold;
    
    &:hover {
      color: #6743DF;
      opacity: .7;
    }
    
    &:before {
      content: '';
      display: block;
      width: 20px;
      height: 20px;
      margin-right: 5px;
      background: url(${downloadImg}) center no-repeat;
    }
  }
`;

const Select = styled.div`
  margin-left: 10px;
  position: relative;
  
  &:after {
      content: '';
      display: block;
      height: 5px;
      width: 5px;
      position: absolute;
      background: url(${arrowDownImg}) center;
      background-size: cover;
      right: 25%;
      top: 50%;
      transform: translateY(-50%);
    }
  
  select {
    padding: 6px 22px 6px 15px;
    background: #fff;
    border: 1px solid #EDF0FE;
    outline: none;
    border-radius: 18px;
    appearance: none;
  }
`;

const Book = styled.div`
  display: flex;
  padding: 50px 0 0;

  @media(max-width: 550px) {
    flex-direction: column;
    
    ${BookImage} {
      order: 2;
    }
    
    ${BookDesc} {
      order: 1;
    }
  }
`;

export class Books extends Component {
    state = {
        current: 0,
        lang: 'en',
        books: [
            {title: 'Executive Summary', img: cover1, link: {title: 'Download Hive’s Executive Summary', id: 'executive-summary'}, desc: `
            The Hive Executive Summary provides an overview of the key challenges facing the identity 
            market, and how Hive's decentralized identity management ecosystem built on the blockchain 
            is helping reimagine identity layer by giving ownership of data back to the user. It also 
            provides a general description of the platform's architecture. This version is currently 
            available in English and Chinese.
            `},
            {title: 'Whitepaper', img: cover2, link: {title: 'Download Hive’s Whitepaper', id: 'whitepaper'}, desc: `
            The Hive Whitepaper explains in-depth some of the key challenges facing the identity market, and how Hive's 
            decentralized identity management ecosystem built on the blockchain is helping reimagine identity layer by 
            giving ownership of data back to the user. Architecture of the platform, blockchain, token economics and 
            future vision are also explained in great detail. This version is currently available in English and Chinese.
            `},
            {title: 'Onepager', img: cover3, link: {title: 'Download Hive’s Onepager', id: 'onepager'}, desc: `
            The Hive Onepager provides a high-level overview of the key challenges facing the identity market, and how 
            Hive's decentralized identity management ecosystem built on the blockchain is helping reimagine identity 
            layer by giving ownership of data back to the user. This version is currently available in English and Chinese.
            `},
        ]
    };

    onClick = (index) => {
        this.setState({current: index});
    };

    onSelect = (event) => {
        this.setState({lang: event.target.value});
    };

    buildLink() {
        const {link} = this.getCurrentBook();

        return `downloads/${link.id}-${this.state.lang}-v1.2.pdf`
    }

    getCurrentBook = () => {
        return this.state.books[this.state.current];
    };

    render() {
        const book = this.getCurrentBook();

        return (
            <BooksContainer>
                <BooksTabs>
                    {this.state.books.map((item, index) => <BooksTabsItem
                        key={index}
                        onClick={() => this.onClick(index)}
                        active={index === this.state.current}><span>{item.title}</span></BooksTabsItem>)
                    }
                </BooksTabs>
                <Book>
                    <BookImage><img src={book.img} alt=""/></BookImage>
                    <BookDesc>
                        <h5>{book.title}</h5>
                        <ChooseLang>
                            <label>Choose language</label>
                            <Select>
                                <select onChange={this.onSelect} name="lang" id="lang-select">
                                    <option value="eu">En</option>
                                    <option value="cn">Cn</option>
                                </select>
                            </Select>
                        </ChooseLang>
                        <p>{book.desc}</p>
                        <a href={this.buildLink()}>{book.link.title}</a>
                    </BookDesc>
                </Book>
            </BooksContainer>
        );
    }
}


