import React, {Component} from 'react';
import styled from 'styled-components';

import * as Sections from 'features/sections';
import heroBgImage from 'assets/images/bg/header-mosaic-bg.png';

const AppWrapper = styled.main`
  background-size: 100%;
  background: url(${heroBgImage}) top center;
  background-repeat-y: no-repeat;
`;

class App extends Component {
    render() {
        return (
            <AppWrapper className="app">
                <Sections.Header/>
                <Sections.Hero/>
                <Sections.PrivateSale/>
                <Sections.Problems/>
                <Sections.MicrosoftQuote/>
                <Sections.HiveSolution/>
                <Sections.KeyAdvantages/>
                <Sections.UniqueFeatures/>
                <Sections.HiveVault/>
                <Sections.Technology/>
                <Sections.Market/>
                <Sections.TokenGeneration/>
                <Sections.Roadmap/>
                <Sections.Leadership/>
                <Sections.Partners/>
                <Sections.Footer/>
            </AppWrapper>
        );
    }
}

export default App;



