import React from 'react';
import styled from 'styled-components';

import {SkewSection, Container, IconedList, SectionHeaderWhite} from 'ui';
import technologyImg from 'assets/images/technology.svg';
import icon1 from 'assets/images/technology/icon1-technology.svg';
import icon2 from 'assets/images/technology/icon2-technology.svg';

const Column = styled.div`
  width: ${p => p.width ? p.width : 100}%;
  padding: 0 20px;
  
  ${p => p.center && `
    display: flex;
    justify-content: center;
    align-items: center;
  `};
`;

const SubHeader = SectionHeaderWhite.extend`
  margin-bottom: 50px;

  &:before {
    display: none;
  }
`;

const Text = styled.p`
  color: white;
  margin-bottom: 32px;
  line-height: 1.375rem;
`;

const ImgContainer = styled.div`
  width: 80%;
 
  img {
    width: 100%
  }
`;

const TextColumn = Column.extend`
  width: 45%;
`;

const ImgColumn = Column.extend`
  width: 55%;
`;

const TopRow = styled.div`
  display: flex;
  
  @media (max-width: 860px) {
    flex-direction: column;
    
    ${TextColumn}, ${ImgColumn} {
      width: 100%;
    }
  }
`;

export const Technology = () => (
    <SkewSection padding='100px 0 100px' margin='170px 0 100px' secondBg='#9879FF' firstBg='#925DFB'>
        <Container dir='column' align='center'>
            <SectionHeaderWhite theme='white' margin='0'>Technology:</SectionHeaderWhite>
            <SubHeader color='white'>How do you combine Identity and Blockchain?</SubHeader>
            <TopRow>
                <TextColumn>
                    <Text>
                        Hive is built using a hybrid blockchain model - a “private permissioned”
                        ledger which periodically anchors to public ledgers - which can deliver the
                        best of both worlds: public access with trusted governance.
                    </Text>
                    <Text>
                        The backbone of Hive ecosystem is a private Hyperledger-based blockchain,
                        storing: (1) profile information, (2) attestations of users' claims, and (3)
                        entities' reputation scores. No PII or hashed PII is stored on the ledger.
                        Only attestations marking the fact that the claim has been verified/certified.
                    </Text>
                    <Text>
                        The second blockchain can be any public blockchain, to which the attestations
                        are periodically anchored to ensure data integrity (Ethereum, Bitcoin, EOS,
                        NEM, etc.). In general, the solution is built in a blockchain platform
                        agnostic manner, to future proof for evolving and emerging blockchain
                        technology innovations and maximum interoperability.
                    </Text>
                </TextColumn>
                <ImgColumn center>
                    <ImgContainer>
                        <img src={technologyImg} alt=""/>
                    </ImgContainer>
                </ImgColumn>
            </TopRow>
            <Column>
                <IconedList>
                    <IconedList.Item descColor='white' img={icon1}>
                        Verifiable credentials are cryptographically shared between peers at the edges of the
                        network. In order to protect the users during the transfer of sensitive data, Hive is
                        using the Public-key cryptography, also known as asymmetric cryptography: a cryptographic
                        technique that enables entities to communicate securely through encryption and subsequent
                        decryption of data.
                    </IconedList.Item>
                    <IconedList.Item descColor='white' img={icon2}>
                        Users will store all of their data on a personal device using the Hive Vault mobile
                        application. To protect data, users can optionally (with their explicit consent) have
                        it backed up to a personal cloud of choice (e.g. iCloud) or a distributed storage platform.
                        Hive will provide such a distributed platform out of the box, and it’s based on the
                        InterPlanetary File System (IPFS).
                    </IconedList.Item>
                </IconedList>
            </Column>
        </Container>
    </SkewSection>
);


