import React from 'react';
import styled from 'styled-components';
import { ResponsivePie } from '@nivo/pie'

import {Container, SectionHeaderWhite, SkewSection, AdaptiveBr, PieChart, Books} from 'ui';

const TokenItem = styled.li`
  padding: 10px 20px;
  max-width: 479px;
  
  h5 {
    margin: 0;
    font-size: 1.125rem;
    font-weight: 900;
  }
  
  p {
    margin: 5px 0;
    font-size: .875rem;
  }
`;

const TokensItems = styled.ul`
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: white;
  list-style: none;
  
  @media (max-width: 760px) {
    flex-direction: column;
    
    ${TokenItem} {
      display: flex;
      
      h5 {
        min-width: 210px;
      }
    }
  }
`;

const BooksContainer = styled.div`
  margin-top: 50px;
  width: 80%;
  margin-bottom: -25%;
  
  @media (max-width: 700px) {
    width: 100%;
  }
`;

export const TokenGeneration = () => (
    <SkewSection padding='100px 0 100px' margin='170px 0 200px' secondBg='#9879FF' firstBg='#925DFB'>
        <Container dir='column' align='center'>
            <SectionHeaderWhite>Token Generation <AdaptiveBr maxWidth='500px'/> Event (TGE)</SectionHeaderWhite>
            <TokensItems>
                <TokenItem>
                    <h5>Name</h5>
                    <p>Hive ID (HID)</p>
                </TokenItem>
                <TokenItem>
                    <h5>Total Supply</h5>
                    <p>700 000 000 (no new tokens will ever be created)</p>
                </TokenItem>
                <TokenItem>
                    <h5>Token Value</h5>
                    <p>1 HID = 0.10 USD</p>
                </TokenItem>
                <TokenItem>
                    <h5>Soft Cap</h5>
                    <p>$5 000 000</p>
                </TokenItem>
                <TokenItem>
                    <h5>Hard Cap</h5>
                    <p>$25 000 000</p>
                </TokenItem>
            </TokensItems>
            <TokensItems>
                <TokenItem>
                    <h5>Bonus for Large Purchasers</h5>
                    <p>Purchasers who commit to purchase tokens during the Token Sale in the amount
                        equal or greater than 500,000 HID, will be eligible for a bonus. All such
                        bonuses will be discussed individually with each potential buyer.</p>
                </TokenItem>
                <TokenItem>
                    <h5>Major Exchanges Listing</h5>
                    <p>Q4 2018</p>
                </TokenItem>
                <TokenItem>
                    <h5>Unsold Tokens</h5>
                    <p>Unsold Tokens</p>
                </TokenItem>
            </TokensItems>
            {/*<PieChart/>*/}
            <BooksContainer>
                <Books/>
            </BooksContainer>
        </Container>
    </SkewSection>
);


