import React, {Component} from 'react';
import styled from 'styled-components';

import {Container, SectionHeaderWhite, Logo} from 'ui';
import pattren from 'assets/images/pattern-footer-small.png';
import logoImage from 'assets/images/logo.png';

import twitter from 'assets/images/social/twitter.svg';
import facebook from 'assets/images/social/facebook.svg';
import linkedin from 'assets/images/social/linkedin.svg';
import telegram from 'assets/images/social/telegram.svg';
import wechat from 'assets/images/social/wechat.svg';
import angel from 'assets/images/social/angel.co.svg';
import crunchbase from 'assets/images/social/crunchbase.svg';

const Section = styled.footer`
  background-color: #925DFB;
  background-image: url(${pattren});
  background-repeat: no-repeat;
  background-position: 50% -155px;
  padding-top: 200px;
`;

const SectionSubHeader = styled.p`
  color: #fff;
  font-size: 1.125rem;
  margin: 0;
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40%;
  
  @media (max-width: 960px) {
    width: 100%;
  }
`;

const Form = styled.form`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 80%;
  margin-top: 70px;
  
  @media (max-width: 460px) {
    width: 100%;
  }
`;

const TextField = styled.div`
  display: flex;
  flex-direction: column;
  color: white;
  max-width: 316px;
  width: ${p => p.width ? p.width : '100%'};
  margin: 20px 0;
  
  input {
    margin-top: 8px;
    padding: 13px 16px;
    border: 1px solid rgba(255, 255, 255, 0.44);
    border-radius: 4px;
    outline: none;
    background-color: rgba(255, 255, 255, 0.11);
    box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
    box-sizing: border-box;
    color: white;
    
    &::placeholder {
      color: white;
      opacity: .5;
    }
  }
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  padding: 35px;
  width: 100%;
`;

const Button = styled.button`
  color: #6743DF;
  box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
  transition: all .20s ease;
  cursor: pointer;
  padding: 12px 24px;
  border-radius: 24px;
  font-weight: 900;
`;

const RadioButton = styled.div`
  padding-left: 10px;

  label {
    display: block;
    padding: 13px 16px;
    border: 1px solid rgba(255, 255, 255, 0.44);
    border-radius: 4px;
    outline: none;
    background-color: rgba(255, 255, 255, 0.11);
    box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
    box-sizing: border-box;
    color: white;
    transition: all .2s;
    
    &:hover {
      border: 1px solid rgba(255,255,255,0.81);
    }
  }
  
  input {
    display: none;
  
    &:checked + label {
      background-color: #fff;
      color: rgba(103, 67, 223);
    }
  }
`;

const InputField = styled.div`
  display: flex;
  max-width: 316px;
  
  @media (max-width: 460px) {
    max-width: 500px;
  }
`;

const RadioContainer = styled.div`
  margin: 20px 0;
  
  & > label {
    display: block;
    padding-left: 9px;
    margin-bottom: 8px;
    color: white;
  }
`;

const SocialsList = styled.ul`
  display: flex;
  list-style: none;
  padding-bottom: 35px;
`;

const SocialsItem = styled.li`
  padding: 10px 10px;
  opacity: .7;
  transition: all .30s ease;
  
  &:hover {
    opacity: 1;
  }
  
  a {
    display: block;
  }
`;

const FooterSection = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 16px 0;
  width: 100%;
  border-top: 1px solid rgba(255, 255, 255, 0.09);
`;

const FooterNav = styled.div`
  display: flex;
  color: white;
  font-size: .75rem;
  
  p {
    margin: 0;
  }
  
  & > * {
    opacity: .7;
    padding-left: 20px;
  }
  
  a {
    transition: all .30s ease;
    
    &:hover {
      opacity: 1;
    }
  }
`;

export class Footer extends Component {
    render() {
        return (
            <Section>
                <Container dir='column' align='center'>
                    <SectionHeaderWhite notIcon>Hive Token Pre-sale Form</SectionHeaderWhite>
                    <SectionSubHeader>Join the revolution in digital identity. Be the owner of your data!</SectionSubHeader>
                    <Form>
                        <Column>
                            <TextField>
                                <label htmlFor="firstName">First name</label>
                                <input type="text" name='firstName' id='firstName' placeholder='John'/>
                            </TextField>
                            <TextField>
                                <label htmlFor="firstName">Last name</label>
                                <input type="text" name='lastName' id='lastName' placeholder='Smith'/>
                            </TextField>
                            <TextField>
                                <label htmlFor="referred">Who referred you?</label>
                                <input type="text" name='referred' id='referred' placeholder='Who referred you?'/>
                            </TextField>
                        </Column>
                        <Column>
                            <TextField>
                                <label htmlFor="email">Email Address</label>
                                <input type="text" name='email' id='email' placeholder='johnsmith@mail.ru'/>
                            </TextField>
                            <InputField>
                                <TextField width='53%'>
                                    <label htmlFor="amount">Amount</label>
                                    <input type="number" name='amount' id='amount' placeholder='1 000?'/>
                                </TextField>
                                <RadioContainer>
                                    <label>Is the amount in</label>
                                    <InputField>
                                        <RadioButton>
                                            <input type="radio" name='token' value='eth' id='eth'/>
                                            <label htmlFor="eth">ETH</label>
                                        </RadioButton>
                                        <RadioButton>
                                            <input type="radio" name='token' value='btc' id='btc'/>
                                            <label htmlFor="btc">BTC</label>
                                        </RadioButton>
                                    </InputField>
                                </RadioContainer>
                            </InputField>
                        </Column>
                        <ButtonContainer>
                            <Button>Send Information</Button>
                        </ButtonContainer>
                    </Form>
                    <SocialsList>
                        {socials.map(item => (
                            <SocialsItem key={item.img}>
                                <a href={item.href}><img src={item.img} alt=""/></a>
                            </SocialsItem>
                        ))}
                    </SocialsList>
                    <FooterSection>
                        <div>
                            <Logo img={logoImage} src='/' alt='Hive' width='76px'/>
                        </div>
                        <FooterNav>
                            <p>© 2018 Hive Identity Technologies</p>
                            <a href="/terms">Terms</a>
                            <a href="/privacy">Privacy</a>
                        </FooterNav>
                    </FooterSection>
                </Container>
            </Section>
        );
    }
}

const socials = [
    {img: twitter, href: ''},
    {img: facebook, href: ''},
    {img: linkedin, href: ''},
    {img: telegram, href: ''},
    {img: wechat, href: ''},
    {img: angel, href: ''},
    {img: crunchbase, href: ''},
];


