import React from 'react';
import styled from 'styled-components';

import {Timer, Container} from 'ui';
import {colors} from 'ui/theme';
import timeImg from 'assets/images/combined-shape.png';

const PrivateSaleWrapper = styled.div`
  padding: 110px 0;
`;

const SaleTitleWrapper = styled.div`
  display: flex;
  align-items: center;
  
  img {
    margin-right: 20px;
  }
`;

const SaleTitle = styled.h3`
  font-size: 2rem;
  font-weight: bold;
  color: ${colors.main};
`;

const SaleDescription = styled.p`
  color: #4a527d;
  width: 85%;
`;


const Button = styled.button`
  background-color: ${colors.main};
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  border-radius: 30px;
  padding: 15px 90px;
  border: none;
  cursor: pointer;
  outline: none;
  box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
`;

const CardFooter = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 50px;
  
  @media (max-width: 990px) {
   flex-direction: column;
   align-items: center;
   
  
    ${Button} {
      width: 100%;
      margin-top: 50px;
    }
  }
  
  @media (max-width: 650px) {
   font-size: .7rem;
  }
  
  @media (max-width: 370px) {
   font-size: .5rem;
   
   ${Button} {
      margin-left: 0;
      margin-right: 0;
    }
  }
`;

const SaleCard = styled.div`
  width: 80%;
  margin: 0 auto;
  padding: 50px 130px; 
  box-shadow: 0 0 11px rgba(47, 21, 136, 0.07), 0 15px 26px rgba(47, 21, 136, 0.06);
  
  @media (max-width: 1150px) {
    padding: 50px 60px;
  }
  
  @media (max-width: 990px) {
    padding: 50px 30px;
  }
  
  @media (max-width: 650px) {
    width: 100%;
  }
`;

export const PrivateSale = () => (
    <PrivateSaleWrapper>
        <Container>
            <SaleCard>
                <SaleTitleWrapper>
                    <img src={timeImg} alt="time"/>
                    <SaleTitle>Private Sale OPEN</SaleTitle>
                </SaleTitleWrapper>
                <SaleDescription>In 2017, there were 16.7 million victims of identity fraud, a record high that followed a previous record the year before.</SaleDescription>
                <CardFooter>
                    <Timer size='3.5em' seconds='70000' inverse/>
                    <Button>Join Us</Button>
                </CardFooter>
            </SaleCard>
        </Container>
    </PrivateSaleWrapper>
);


