import {colors} from "ui/theme";
import styled from "styled-components";

import titleImagePurple from 'assets/images/heading.png';
import titleImageWhite from 'assets/images/heading-white.svg';

export const SectionHeader = styled.h3`
    color: ${p => p.color ? p.color : colors.main};
    font-size: 2rem;
    position: relative;
    margin-bottom: 20px;
    ${p => p.margin && `margin: ${p.margin};`};
    
    &:before {
      content: '';
      display: ${p => p.notIcon ? 'none' : 'blocks'};
      width: 50px;
      height: 43px;
      position: absolute;
      left: -29px;
      top: -34px;
      background: url(${titleImagePurple});
      background-repeat: no-repeat;
      background-size: cover;
      font-weight: 900;
    }
`;

export const SectionHeaderWhite = SectionHeader.extend`
  color: white;
  font-weight: 900;
  
  &:before {
    background-image: url("${titleImageWhite}");
  }
`;


