import React from 'react';
import styled from 'styled-components';

import solutionImg from 'assets/images/solution.svg';
import {Container, SectionHeader} from 'ui';

const Section = styled.section`
  margin: 50px 0;
`;

const SectionDescription = styled.p`
  color: #3E466D;
  font-size: 1.125rem;
  text-align: center; 
  max-width: 1024px;
  line-height: 1.75rem;
`;

const SolutionMap = styled.img`
  width: 100%;
  max-width: 992px;
  margin-top: 50px;
`;

export const HiveSolution = () => (
    <Section>
        <Container dir='column' align='center'>
            <SectionHeader>Hive Solution</SectionHeader>
            <SectionDescription>
                Hive is a complete self-sovereign identity management solution that institutes a
                system of relationships between businesses and consumers.
            </SectionDescription>
            <SectionDescription>
                By integrating an innovative mechanism to store identifiers, keys, pointers and proofs without relying
                on centralized authorities, it allows individuals and organizations to process a sequence of
                transactions to reliably prove their identities.
            </SectionDescription>
            <SolutionMap src={solutionImg} alt='Solution map'/>
        </Container>
    </Section>
);


