import React from 'react';
import styled from 'styled-components';

import MicrosoftImg from 'assets/images/quote/microsoft.svg';
import {QuoteCard, Container} from 'ui';

const SectionWrapper = styled.section`
  padding: 100px 0;
`;

const QuoteCardContainer = styled.div`
  width: 80%;
  
  @media (max-width: 500px) {
    width: 100%;
  }
`;

export const MicrosoftQuote = () => (
    <SectionWrapper>
        <Container justify='center'>
            <QuoteCardContainer>
                <QuoteCard
                    width='100%'
                    img={MicrosoftImg}
                    author='The Laws of Identity, Kim Cameron'
                    authorPosition='Chief Architect of Identity for Microsoft'
                >
                    The Internet was built without a way to know who and what you are connecting to.
                    This limits what
                    we can do with it and exposes us to growing dangers. If we do nothing, we will face rapidly
                    proliferating episodes of theft and deception which will cumulatively erode public trust in the
                    Internet.
                </QuoteCard>
            </QuoteCardContainer>
        </Container>
    </SectionWrapper>
);


