import styled from "styled-components";

const ClipPath = styled.div`
  position: relative;
  height: 200px;
  width: 100%;
  ${p => p.zindex && 'z-index: ' + p.zindex}
`;

const Clip = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  clip-path: polygon(${p => p.path});
  background-color: ${p => p.color}; 
`;

ClipPath.Item = Clip;

export {ClipPath};


