import React from 'react';
import styled from 'styled-components';

import {Container, SectionHeader, QuoteCard} from 'ui';
import elips1 from 'assets/images/market/elips1-market.svg';
import elips2 from 'assets/images/market/elips2-market.svg';
import horizontalArrow from 'assets/images/market/big-arrow.svg';
import verticalArrow from 'assets/images/market/big-arrow-down.svg';
import googleImg from 'assets/images/google.svg';

const Chart = styled.div`
  width: 35%;
  
  img {
    width: 100%;
  }
`;

const ChartDescList = styled.ul`
  list-style: none;
`;

const ChartDescItem = styled.li`
  margin: 20px;

  h5, p {
    font-size: .875rem;
    color: #3E466D;
    
    @media (max-width: 880px) {
      font-size: .6875rem;
    }
    
    @media (max-width: 750px) {
      font-size: .875rem;
    }
  }

  h5 {
    margin: 0;
    font-weight: 700;
    position: relative;

    &:before {
      content: '';
      display: block;
      width: 7px;
      height: 7px;
      background-color: ${p => p.color};
      border-radius: 50%;
      position: absolute;
      left: -20px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  p {
    margin: 10px 0 0;
  }
`;

const ArrowContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  img {
    width: 100%;
    max-height: 100px;
  }
  
  h5 {
    margin: 0;
    font-size: 1.5rem;
    font-weight: 900;
    color: #6743DF;
    padding: 5px 15px;
  }
  
  p {
    margin: 5px 15px;
    color: #3E466D;
    font-weight: 700;
  }
`;

const HorizontalArrow = styled.img`
  @media (max-width: 750px) {
    display: none;  
  }
`;

const VerticalArrow = styled.img`
  display: none;
  
  @media (max-width: 750px) {
    display: block;  
  }
`;

const Section = styled.section`
  padding: 100px 0;
`;

const Label = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 33px;
  
  h5, p {
    margin: 0;
  }
  
  h5 {
    font-size: 2.75rem;
    font-weight: 900;
    color: #6743DF;
  }
  
  p {
    padding: 15px 0;
    font-size: 1.125rem;
    font-weight: 700;
    color: #504A8E;
  }
`;

const ChartDesc = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  padding-top: 10%;
`;

const ChartContainer = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  
  @media (max-width: 750px) {
    flex-direction: column;
    
    ${ChartDesc} {
      width: 100%;
    }
    
    ${Chart} {
      margin: 50px 0;
      width: 100%;
    }
    
    ${ArrowContainer} {
      flex-direction: row;
    }
    
    ${Label} {
      h5 {
        font-size: 2rem;
        font-weight: 700;
      }
    }
  }
`;

const QuoteContainer = styled.div`
  margin: 150px 0 0;
  width: 80%;
  
  @media (max-width: 500px) {
    width: 100%;
  }
`;

export const Market = () => (
    <Section>
        <Container dir='column' align='center'>
            <SectionHeader>Market</SectionHeader>
            <ChartContainer>
                <Chart>
                    <Label>
                        <h5>$42.5B</h5>
                        <p>Current market size</p>
                    </Label>
                    <img src={elips1} alt=""/>
                </Chart>
                <ChartDesc>
                    <ChartDescList>
                        <ChartDescItem color='#E622A3'>
                            <h5>Identity Management – $20B</h5>
                            <p>(2023 – $30B, CAGR – 8.45%)</p>
                        </ChartDescItem>
                        <ChartDescItem color='#1AD954'>
                            <h5>Identity Verification – $13B</h5>
                            <p>(2023 – $40B, CAGR – 25%)</p>
                        </ChartDescItem>
                    </ChartDescList>
                    <ArrowContainer>
                        <h5>+15.7%</h5>
                        <HorizontalArrow src={horizontalArrow}/>
                        <VerticalArrow src={verticalArrow}/>
                        <p>CAGR</p>
                    </ArrowContainer>
                    <ChartDescList>
                        <ChartDescItem color='#FF9313'>
                            <h5>Identity Protection – $6B</h5>
                            <p>(2023 – $12B, CAGR – 15%)</p>
                        </ChartDescItem>
                        <ChartDescItem color='#22ADE6'>
                            <h5>Employment Screening – $3.5B</h5>
                            <p>(2023 – $6B, CAGR – 11.45%)</p>
                        </ChartDescItem>
                    </ChartDescList>
                </ChartDesc>
                <Chart>
                    <Label>
                        <h5>$88B</h5>
                        <p>Expected market size by 2023</p>
                    </Label>
                    <img src={elips2} alt=""/>
                </Chart>
            </ChartContainer>
            <QuoteContainer>
                <QuoteCard
                    width='100%'
                    img={googleImg}
                    author='Eric Schmidt'
                    authorPosition='Google Chairman'
                >
                    Your online identity in the future is unlikely to be a simple Facebook page; instead
                    it will be a constellation of profiles, from every online activity. Identity will be
                    the most valuable commodity for citizens in the future, and it will
                    exist primarily online.
                </QuoteCard>
            </QuoteContainer>
        </Container>
    </Section>
);


