import React from 'react';
import styled from 'styled-components';

import checkImg from 'assets/images/check.svg'
import {Container, SectionHeader, AdaptiveBr} from 'ui';

const Section = styled.section`
  padding: 120px 0;
`;

const SectionDescription = styled.p`
  color: #3E466D;
  font-size: 1.125rem; 
  max-width: 1024px;
  line-height: 1.75rem;
  text-align: left;
`;

const Column = styled.div`
  width: 50%; 
  padding: 0 30px;
  
  @media(max-width: 760px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 0;
  }
`;

const CheckedList = styled.ul`
  list-style: none;
`;

const CheckedListItem = styled.li`
  padding: 20px 0;
  border-left: 2px solid #ECF0FF;
  
  &:first-child {
    padding-top: 80px;
  }
  
  &:last-child {
    padding-bottom: 60px;
  }
  
  p {
    padding: 0 60px;
    color: #3E466D;
    font-size: .875rem;
    line-height: 1.375rem;
  }
  
  h4 {
    padding: 0 60px;
    position: relative;
    font-weight: 700;
    color: #3E466D;
  
    &:before {
      content: '';
      display: block; 
      height: 32px;
      width: 32px;
      background-image: url(${checkImg});
      background-size: 120% 120%;
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      left: 0;
      bottom: 0;
      transform: translateX(-50%) translateY(100%);   
    }
  }
  
  @media(max-width: 760px) {
    border-left: none;
    
    h4 {
      padding: 0 55px;
      
      &:before { 
        transform: translateY(25%);
      }
    }
    
    p {
      padding: 20px 0;
    }
  }
`;

export const KeyAdvantages = () => (
    <Section>
        <Container wrap='wrap'>
            <Column>
                <SectionHeader>Key Advantages <AdaptiveBr maxWidth='500px'/> for Consumers</SectionHeader>
                <SectionDescription>
                    Hive is a Digital Identity Management Ecosystem with a user-centric architecture. Through a user's
                    digital identity (and with their consent) all of the operations on the Network are coordinated.
                </SectionDescription>
                <SectionDescription>
                    All Personally Identifiable Information (PII) is kept off-ledger behind service endpoints and
                    stored locally under user’s full control on their devices.
                </SectionDescription>
                <SectionDescription>
                    With such privacy architecture, PII may be exchanged on a private, peer-to-peer basis using
                    communications channels identified and secured by asymmetric encryption. This also enables entities
                    and relying parties to implement the regulatory procedures, such as compliance with GDPR, as no PII
                    will be recorded on an immutable ledger.
                </SectionDescription>
                <SectionDescription>
                    A user’s virtual identity profile (stored on Blockchain) is unique and belongs only to them. The
                    integrity of the information is also confirmed through anchoring of the private and public blockchains.
                </SectionDescription>
            </Column>
            <Column>
                <CheckedList>
                    <CheckedListItem>
                        <h4>Identity verification</h4>
                        <p>Hive users are able to build several levels of digital identity. During the initial
                            onboarding, they create their online legal identity and verify claims (i.e PII including
                            passport, driver’s license, birth certificate, etc.) through Hive Vendors.</p>
                    </CheckedListItem>
                    <CheckedListItem>
                        <h4>Identity verification</h4>
                        <p>Hive users are able to build several levels of digital identity. During the initial
                            onboarding, they create their online legal identity and verify claims (i.e PII including
                            passport, driver’s license, birth certificate, etc.) through Hive Vendors.</p>
                    </CheckedListItem>
                    <CheckedListItem>
                        <h4>Identity verification</h4>
                        <p>Hive users are able to build several levels of digital identity. During the initial
                            onboarding, they create their online legal identity and verify claims (i.e PII including
                            passport, driver’s license, birth certificate, etc.) through Hive Vendors.</p>
                    </CheckedListItem>
                    <CheckedListItem>
                        <h4>Identity verification</h4>
                        <p>Hive users are able to build several levels of digital identity. During the initial
                            onboarding, they create their online legal identity and verify claims (i.e PII including
                            passport, driver’s license, birth certificate, etc.) through Hive Vendors.</p>
                    </CheckedListItem>
                </CheckedList>
            </Column>
        </Container>
    </Section>
);


