import React from 'react';
import styled from 'styled-components';

import QuoteDown from 'assets/images/quote/quotes-down.svg';
import QuoteUp from 'assets/images/quote/quotes-up.svg';

const QuoteIcon = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 40px;
  
  img {
    width: 100%;
  }
`;

const QuoteDetails = styled.div`
  display: flex;
  flex-direction: column;
  padding: 0 40px;
  width: 70%; 
`;

const QuoteText = styled.p`
  color: #3E466D;
  font-style: italic;
  opacity: 0.8;
  letter-spacing: 0.1px;
  line-height: 1.5rem;
  padding: 17px 0;
`;

const QuiteAuthor = styled.div`
  line-height: 1.5rem;
  font-weight: 600;
  color: #3E466D;
`;

const QuoteAuthorPosition = styled.div`
  font-size: 0.875rem;
  line-height: 1.5rem;
  color: #3E466D;
  font-weight: 400;
`;

const FirstQuote = styled.span`
  position: relative;

  &:after {
    content: '';
    position: absolute;
    display: block;
    height: 34px;
    width: 34px;
    left: 0; 
    top: 0;
    background-image: url(${QuoteUp});
    transform: translateX(-100%) translateY(-100%);
  }
`;

const LastQuote = styled.span`
  position: relative;

  &:after {
    content: '';
    position: absolute;
    display: block;
    height: 34px;
    width: 34px;
    right: 0;
    bottom: 0;
    background-image: url(${QuoteDown});
    transform: translateX(100%) translateY(100%);
  }
`;

const QuoteCardWrapper = styled.div`
  display: flex;
  width: ${p => p.width ? p.width : '80%'};
  box-shadow: 0 15px 26px rgba(50, 50, 93, 0.06), 0 0 11px rgba(0, 0, 0, 0.07);
  padding: 5% 20px;
  border-radius: 8px;
  
  @media (max-width: 750px) {
    flex-direction: column;
    
    ${QuoteDetails} {
      width: 100%;  
    }
    
    ${QuoteIcon} {
      max-width: 350px;
        margin: 20px 0;
    }
  }
`;

export const QuoteCard = ({img, children, author, authorPosition, ...props}) => (
    <QuoteCardWrapper {...props}>
        <QuoteIcon>
            <img src={img} alt="Quote card img"/>
        </QuoteIcon>
        <QuoteDetails>
            <QuoteText>
                <FirstQuote/>{children}<LastQuote/>
            </QuoteText>
            <QuiteAuthor>{author}</QuiteAuthor>
            <QuoteAuthorPosition>{authorPosition}</QuoteAuthorPosition>
        </QuoteDetails>
    </QuoteCardWrapper>
);


