import React from 'react';
import styled from 'styled-components';

const LogoLink = styled.a`
  display: block;
  padding: ${p => p.padding};
  width: ${p => p.width};
  cursor: pointer;

  img {
    width: 100%;
  }
`;

export const Logo = ({href, img, alt, ...props}) => (
    <LogoLink href={href} {...props}>
        <img src={img} alt={alt}/>
    </LogoLink>
);


