import React, {Component} from 'react';
import styled from 'styled-components';
import s from './chart.svg';

import {ProportionalContainer} from 'ui';

const ChartContainer = styled.div`
  display: flex;
  justify-content: center;
  padding-top: 70px;
`;

export class PieChart extends Component {
    state = {
        data: [
            {
                "id": "c",
                "label": "c",
                "value": 253,
                "color": "hsl(346, 70%, 50%)"
            },
            {
                "id": "stylus",
                "label": "stylus",
                "value": 143,
                "color": "hsl(346, 70%, 50%)"
            },
            {
                "id": "ruby",
                "label": "ruby",
                "value": 67,
                "color": "hsl(269, 70%, 50%)"
            },
            {
                "id": "python",
                "label": "python",
                "value": 466,
                "color": "hsl(68, 70%, 50%)"
            },
            {
                "id": "css",
                "label": "css",
                "value": 382,
                "color": "hsl(14, 70%, 50%)"
            }
        ]
    };

    onClick = (event) => {
        console.log(event);
        this.setState({data: this.state.data.slice(0, -1)});
    };

    render() {
        return (
            <ProportionalContainer height='50'>
                <ChartContainer>
                    <object data={s} type=""/>
                </ChartContainer>
            </ProportionalContainer>
        );
    }
}




