import styled from 'styled-components';

export const SkewSection = styled.section`
  position: relative;
  padding: ${p => p.padding};
  margin: ${p => p.margin};

  &:before, &:after {
    content: '';
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  
  &:before {
    transform: skewY(-4deg);
    background: ${p => p.secondBg ? p.secondBg : '#F2F2FD'};
  }
  
  &:after {
    transform: skewY(6deg);
    background: ${p => p.firstBg ? p.firstBg : '#F9F9FF'};
  }
`;


